@import "./utils.scss";

.footer {
  padding-top: 24px;
  margin: 0 auto;
  display: flex;
  color: $black;
  @include mobile {
    flex-direction: column;
    height: 140px;
    padding-left: 20px;
    padding-right: 20px;
  }
  @include tablet {
    flex-direction: column;
    max-width: 1220px;
    padding-left: 50px;
    padding-right: 50px;
  }
  @include desktop {
    flex-direction: row;
    height: 100px;
    max-width: 1220px;
    padding-left: 50px;
    padding-right: 50px;
  }
}

.footer img {
  width: 72px;

  @include mobile {
  }
  @include tablet {
  }
  @include desktop {
    margin-bottom: 80px;
  }
}

.footer p {
  @include mobile {
    margin-left: 0;
    margin-top: 12px;
  }
  @include tablet {
    margin-left: 0;
    margin-top: 16px;
  }
  @include desktop {
    margin-top: 0;
    margin-left: 20px;
  }
}
